body {
  font-family: "Roboto Condensed", "Helvetica", sans-serif;
  margin: 0 auto;
  padding: 0; }

img {
  display: block;
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.page-wrapper {
